"use client";

import * as React from "react";
import { FaUser } from "react-icons/fa";
import { Button } from "@/components/ui/button";
import { Command, CommandEmpty, CommandGroup, CommandInput, CommandItem, CommandList } from "@/components/ui/command";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Check, ChevronsUpDown } from "lucide-react";

interface Jury {
  idJury: number;
  nom: string;
}

interface JurySelectorProps {
  selectedJury: string;
  onSelectJury: (jury: string) => void;
}

export default function JurySelector({ selectedJury, onSelectJury }: JurySelectorProps) {
  const [open, setOpen] = React.useState(false);
  const [jurys, setJurys] = React.useState<Jury[]>([]);
  const [loading, setLoading] = React.useState<boolean>(true);
  const [error, setError] = React.useState<string | null>(null);

  // Fetch jury data from the API
  React.useEffect(() => {
    const fetchJurys = async () => {
      try {
        const response = await fetch('http://localhost:5000/api/jurys');
        if (!response.ok) {
          throw new Error('Failed to fetch jury data');
        }
        const data = await response.json();
        setJurys(data);
      } catch (err) {
        setError('Failed to fetch jury data');
      } finally {
        setLoading(false);
      }
    };

    fetchJurys();
  }, []);

  const selectedJuryName = React.useMemo(() => {
    const jury = jurys.find((j) => j.nom === selectedJury);
    return jury ? jury.nom : "S√©lectionner un jury";
  }, [jurys, selectedJury]);

  if (loading) {
    return <div>Chargement...</div>;
  }

  if (error) {
    return <div>{error}</div>;
  }

  return (
    <div className="flex items-center gap-4">
      <FaUser className="h-5 w-5 text-muted-foreground shrink-0" />
      <Popover open={open} onOpenChange={setOpen}>
        <PopoverTrigger asChild>
          <Button
            variant="outline"
            role="combobox"
            aria-expanded={open}
            className="w-full justify-between hover:bg-secondary/50"
          >
            <span className="truncate">{selectedJuryName}</span>
            <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
          </Button>
        </PopoverTrigger>
        <PopoverContent className="w-full p-0">
          <Command>
            <CommandInput placeholder="Rechercher un jury..." />
            <CommandList>
              <CommandEmpty>Aucun jury trouv√©.</CommandEmpty>
              <CommandGroup heading="Jurys disponibles">
                {jurys.map((jury) => (
                  <CommandItem
                    key={jury.idJury}
                    value={jury.nom}
                    onSelect={(currentValue) => {
                      onSelectJury(currentValue);
                      setOpen(false);
                    }}
                  >
                    <Check className={`mr-2 h-4 w-4 ${selectedJury === jury.nom ? "opacity-100" : "opacity-0"}`} />
                    {jury.nom}
                  </CommandItem>
                ))}
              </CommandGroup>
            </CommandList>
          </Command>
        </PopoverContent>
      </Popover>
    </div>
  );
}
