/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable react/jsx-no-undef */
"use client";

import { useState, useEffect } from "react";
import { FaUser } from "react-icons/fa";
import { Button } from "@/components/ui/button";
import { Command, CommandEmpty, CommandGroup, CommandInput, CommandItem, CommandList } from "@/components/ui/command";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Check, ChevronsUpDown } from "lucide-react";
import SubmitButton from "../components/PlanifierSoutenance/SubmitButton";
import RoomSelector from "../components/PlanifierSoutenance/RoomSelector";
import GroupSelector from "../components/PlanifierSoutenance/GroupSelector";

interface Group {
  idGroupe: string;
  nomGroupe: string;
  nbEtudiants: number;
}

interface Jury {
  idJury: number;
  nom: string;
  valideDeliberation: number;
}

interface Room {
  id: string;
  name: string;
}

interface Soutenance {
  date: string;
  time: string;
  location: string;
  jury: string;
  group: string;
}

const PlanifierSoutenance = () => {
  const [newSoutenance, setNewSoutenance] = useState<Soutenance>({
    date: "",
    time: "",
    location: "",
    jury: "",
    group: "",
  });

  const [groups, setGroups] = useState<Group[]>([]);
  const [jurys, setJurys] = useState<Jury[]>([]);
  const [loading, setLoading] = useState<boolean>(true);

  // Static room data
  const rooms: Room[] = [
    { id: "1", name: "Salle 12" },
    { id: "2", name: "Salle 42" },
    { id: "3", name: "Salle 21" },
  ];

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [groupsResponse, jurysResponse] = await Promise.all([
          fetch("http://localhost:5000/api/groups"), // Fetch groups from the backend
          fetch("http://localhost:5000/api/jurys"),
        ]);

        const groupsData = await groupsResponse.json();
        const jurysData = await jurysResponse.json();

        setGroups(groupsData);
        setJurys(jurysData);
      } catch (error) {
        console.error("Error fetching data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const addSoutenance = () => {
    if (
      newSoutenance.date &&
      newSoutenance.time &&
      newSoutenance.location &&
      newSoutenance.jury &&
      newSoutenance.group
    ) {
      alert("Soutenance ajout√©e avec succ√®s !");
      console.log("Nouvelle soutenance:", newSoutenance);
      setNewSoutenance({ date: "", time: "", location: "", jury: "", group: "" });
    } else {
      alert("Veuillez remplir tous les champs !");
    }
  };

  // JurySelector Component
  const JurySelector = ({ selectedJury, onSelectJury }: { selectedJury: string; onSelectJury: (jury: string) => void }) => {
    const [open, setOpen] = useState(false);

    const selectedJuryName = React.useMemo(() => {
      const jury = jurys.find((j) => j.nom === selectedJury);
      return jury ? jury.nom : "S√©lectionner un jury";
    }, [jurys, selectedJury]);

    if (loading) {
      return <div>Chargement...</div>;
    }

    return (
      <div className="flex items-center gap-4">
        <FaUser className="h-5 w-5 text-muted-foreground shrink-0" />
        <Popover open={open} onOpenChange={setOpen}>
          <PopoverTrigger asChild>
            <Button
              variant="outline"
              role="combobox"
              aria-expanded={open}
              className="w-full justify-between hover:bg-secondary/50"
            >
              <span className="truncate">{selectedJuryName}</span>
              <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
            </Button>
          </PopoverTrigger>
          <PopoverContent className="w-full p-0">
            <Command>
              <CommandInput placeholder="Rechercher un jury..." />
              <CommandList>
                <CommandEmpty>Aucun jury trouv√©.</CommandEmpty>
                <CommandGroup heading="Jurys disponibles">
                  {jurys.map((jury) => (
                    <CommandItem
                      key={jury.idJury}
                      value={jury.nom}
                      onSelect={(currentValue) => {
                        onSelectJury(currentValue);
                        setOpen(false);
                      }}
                    >
                      <Check className={`mr-2 h-4 w-4 ${selectedJury === jury.nom ? "opacity-100" : "opacity-0"}`} />
                      {jury.nom}
                    </CommandItem>
                  ))}
                </CommandGroup>
              </CommandList>
            </Command>
          </PopoverContent>
        </Popover>
      </div>
    );
  };

  return (
    <div className="p-8 bg-gray-50 min-h-screen">
      <h1 className="text-4xl font-bold text-gray-800 mb-8 text-center">üìÖ Planification des Soutenances</h1>

      <div className="max-w-4xl mx-auto bg-white p-6 rounded-lg shadow-xl mb-8">
        <h2 className="text-2xl font-semibold text-gray-700 mb-6">üìù D√©finir une nouvelle soutenance</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <GroupSelector
            groups={groups}
            selectedGroup={newSoutenance.group}
            onSelectGroup={(groupId) => setNewSoutenance({ ...newSoutenance, group: groupId })}
          />
          <DateTimePicker
            date={newSoutenance.date}
            time={newSoutenance.time}
            onDateChange={(date: any) => setNewSoutenance({ ...newSoutenance, date })}
            onTimeChange={(time: any) => setNewSoutenance({ ...newSoutenance, time })}
          />
          <RoomSelector
            rooms={rooms}
            selectedRoom={newSoutenance.location}
            onSelectRoom={(location: any) => setNewSoutenance({ ...newSoutenance, location })}
          />
          <JurySelector
            selectedJury={newSoutenance.jury}
            onSelectJury={(jury) => setNewSoutenance({ ...newSoutenance, jury })}
          />
        </div>
        <SubmitButton onClick={addSoutenance} />
      </div>
    </div>
  );
};

export default PlanifierSoutenance;
